import { Plugin } from '@ali/4ever-cangjie';
import { BiPluginConfig } from '../../types';
import createRenderNode from './createRenderNode';
import createRenderEditable from './createRenderEditable';
import onAction from './onAction';

export default function createLaserPointPlugin(config: BiPluginConfig): Plugin {
  const pluginConfig = config.laserPointer;

  return {
    renderNode: createRenderNode(pluginConfig),
    renderEditable: createRenderEditable(pluginConfig),
    onAction,
  };
}
